import { IStore } from "../stores/IStore";
import {
  ISideBarStateProps,
  SideBar as SideBarComponent
} from '../components/SideBar';
import { ComponentClass } from "react";
import { connect } from "react-redux";

export interface ISideBarContainerProps {
}

const mapStateToProps = (state: IStore, ownProps: ISideBarContainerProps): ISideBarStateProps => {
  return {
    selectedWelfareCentre: state.selectedWelfareCentre,
  };
};

export const SideBar: ComponentClass<ISideBarContainerProps> = connect(
  mapStateToProps,
)(SideBarComponent);
